.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.button-card {
  flex: 1;
  align-items: center;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  min-width: 320px;
}

.button-card__header {
  border-bottom: 1px solid #e0e0e0;
  padding-top: 8px;
  padding-bottom: 8px;
}

.button-card__content {
  align-items: center;
  padding: 16px 16px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
